import React, {useState} from 'react';

import './OpenChartButton.css'
import ModalChart from "./ModalChart";

function OpenChartButton({setTimerMethod,unsetTimerMethod, sensorId,sensorName,sensorUnitName}) {
    const [open, setOpen] = useState(false)
    const setTimer=()=>{
        setOpen(false);
        setTimerMethod();
    }

    const unsetTimer=()=>{

        setOpen(true);
        unsetTimerMethod();
    }
    return (
<div>
            <button onClick={() => {unsetTimer()}}>Chart</button>
            <ModalChart
                message="Hello World!"
                isOpen={open}
                sensorId={sensorId}
                sensorName={sensorName}
                sensorUnitName={sensorUnitName}
                onClose={() => {setTimer()}}
            />
</div>
    )
}


export default OpenChartButton;
