import React from 'react'
import './Sensor.css'
import {getUnit,getSensorType,getTimeWithOffset,getSensorBGColorClass} from '../functions'

import OpenChartButton from "./OpenChartButton";


const Sensor=(props)=> {
    const setTimer=()=>{
        props.setTimerMethod();
    }

    const unsetTimer=()=>{
        props.unsetTimerMethod();
    }
        return(
            <div className={getSensorBGColorClass(getTimeWithOffset(props.sensor.lastGoodValueMoment,0))}>
            <div className='sensor-item__title'>{props.sensor.sensorName}</div>
            <div>
                <p>{props.sensor.lastGoodValue}{getUnit(props.sensor.sensorUnitName)}</p>
                <p>{getTimeWithOffset(props.sensor.lastGoodValueMoment,0).toLocaleString()}</p>
                <p>{props.sensor.locationName}</p>
                <p>{props.sensor.sensorId}</p>
                <p>{getSensorType(props.sensor.sensorType)}</p>
            </div>
            <div className="container-center">
                <OpenChartButton  setTimerMethod={setTimer} unsetTimerMethod={unsetTimer} sensorId={props.sensor.id} sensorName={props.sensor.sensorName} sensor={props.sensor} sensorUnitName={props.sensor.sensorUnitName}/>

            </div>
           </div>

        )


}


export default Sensor;
