import React, {Component} from 'react'
import './SensorList.css';
import Sensor from "./Sensor";
import Header from "./Header";



const QUOTE_SERVICE_URL = 'http://home.remlo.ru:8081/api/sensors'

class SensorList extends Component{
    constructor(props) {
        super(props);
        this.timer = null;
        this.state = {
            isFetching: false,
            Sensors: []
        };
        console.log("sensorList created.")
    }

render (){
        return(
            <div className="container-center">
            <div >{Header("Мои датчики")}</div>
            <div className="container">{this.state.isFetching ? 'Fetching Sensors...' :  this.state.Sensors.map(sensor =>
                <Sensor  setTimerMethod={this.startFetch} unsetTimerMethod={this.unsetTimer}  key={sensor.id} sensor={sensor} />
            )}
            </div>

            </div>
)

}

     startFetch=()=>{
         this.fetchSensors()
         this.timer = setInterval(() => this.fetchSensors(), 60000);
         console.log("main timer started")
    }

    unsetTimer=()=>{
        clearInterval(this.timer)
        console.log("main timer stopped")
    }

    componentDidMount() {
       this.startFetch();
    }

    componentWillUnmount() {
        this.unsetTimer();
    }


    fetchSensors = () => {
        this.setState({...this.state, isFetching: true})
        fetch(QUOTE_SERVICE_URL, {
            headers: {
                'Content-Type': 'application/json'
            }})
            //.then(response => console.log(response.text())
            .then(response => response.json())

            .then(result => this.setState({
                    Sensors: result,
                    isFetching: false
                })
            )
            .catch(e => console.log(e));
console.log("log")
    }



}
export default SensorList
