import React, {Component} from 'react'
import ReactDOM from "react-dom";
import TempChart from "./TempChart";
import './ModalChart.css'









const ModalChart =({ message, isOpen, onClose, children, sensorId, sensorName,sensor, sensorUnitName })=> {
        if (!isOpen) return null;
        console.log("Modalchart created.");
        console.log(sensorId);
        return ReactDOM.createPortal(
            <div className="modal">
                    <TempChart sensorId={sensorId} sensorName={sensorName} sensor={sensor} sensorUnitName={sensorUnitName}/>

                    <button className="ButtonLeftTop" onClick={onClose}>[X]</button>
            </div>
            ,document.body);
}


export default  ModalChart;


