import React from "react";

import {VictoryLine, VictoryChart, VictoryAxis, VictoryTheme} from 'victory';
import './chartHolder.css'
import {getUnit} from "../functions";

const DATA_SERVICE_URL="http://home.remlo.ru:8081/api/data/"



class TempChart  extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.timer = null;
        this.sensorId =props.sensorId;
        this.sensorName=props.sensorName;
        this.sensorUnitName=props.sensorUnitName;
        this.sensor=props.sensor;
        this.timeinterval="hour";
        this.state = {
            isFetching: false,
            Data: []
        };
        console.log("TempChart created");
    }

    componentDidMount() {
        this.getChartData()
        this.timer = setInterval(() => this.getChartData(), 60000);
    }

    componentWillUnmount() {
        clearInterval(this.timer)
    }


    getChartData = () => {
        this.setState({...this.state, isFetching: true})
        if (this.sensorId!=null) {
            let url;
            url=DATA_SERVICE_URL + this.sensorId + "/"+this.timeinterval;
            console.log(url);
            fetch(url, {
                headers: {
                    'Content-Type': 'application/json'
                }})
                //.then(response => console.log(response.text())
                .then(response => response.json())

                .then(result => this.setState({
                        Data: result,
                        isFetching: false
                    })

                )
                .catch(e => console.log(e));

            console.log(this.state.Data);
            console.log(this.state.isFetching);
        }
        else
        {
            console.log("Waiting for sensor...")
        }
    }

     render() {


        return (
            <div className="ChartHolder">
                <div>{this.sensorName}</div>
                <div><button className="button" onClick={()=>{this.setimeinterval("hour")}}>Hour</button>
                <button className="button" onClick={()=>this.setimeinterval("day")}>Day</button>
                <button className="button" onClick={()=>this.setimeinterval("week")}>Week</button>
                    <button className="button" onClick={()=>this.setimeinterval("month")}>Month</button></div>

                <VictoryChart
                domainPadding={{ x: 0,y:[35,35] }
                }
                padding={{left:10,right:-10,top:10,bottom:80}}
                theme={VictoryTheme.material}
            >
                <VictoryLine

                    data={this.state.Data}

                    interpolation="natural"
                    y="value"
                    x="moment"

                    style={{
                        data: { fill: "none", opacity: 0.7 },

                        labels: { fontSize: 10 },
                        parent: { border: "1px solid #ccc" },
                        border: "1px solid #ccc",
                        //labels={({ datum }) => datum.y},
                        //labelComponent={<VictoryLabel renderInPortal dy={-20}/>}

                    }}

                 />
                    <VictoryAxis

                        //label="Дата-время"
                        fixLabelOverlap={true}
                        tickFormat={(t) =>(this.timeinterval==="hour"||this.timeinterval==="day")? `${new Date(t).toLocaleTimeString(navigator.language, {hour: '2-digit', minute:'2-digit'})}`:
                            `${new Date(t).toLocaleDateString(navigator.language, {day: '2-digit', month:'2-digit'})}`}
                        style={{

                            //axisLabel: { padding: 10 }
                        }}


                    />
                    <VictoryAxis dependentAxis
                                 label={getUnit(this.sensorUnitName)}
                                 style={{
                                     axisLabel: {fontSize: 20, padding: 40 },
                                     tickLabels: {fontSize: 15, padding: 5},
                                     grid: {stroke: ({ tick }) => tick === 0 ? "red" : "grey"}
                                 }}
                    />

                </VictoryChart></div>);
    }


     setimeinterval(ti) {

            this.timeinterval=ti;
            this.getChartData();
    }
}

export default TempChart;
