
import moment from 'moment'


function getUnit(unitName){
switch (unitName){
    case '%':return "%";
    case 'volt':return "V";
    default: return "\u00b0C";
}
}

function getSensorType(sensorType){
    switch (sensorType){
        case 'temp':return "Температура";break;
        case 'hum':return "Влажность";break;
        case 'volt':return "Напряжение";break;
        default:return "Неизвестный тип";break;
    }
}

function getTimeWithOffset(time,offset){
    return moment(Date.parse(time)).add(offset, 'h').toDate();
}

function getSensorBGColorClass(time){
    let th;
    th=moment(time).add(1,"h");
    if (th<moment(getTimeWithOffset(new Date(moment.now()),0))) {
        return "sensor-item-red"
    }else
    {
        return "sensor-item"
    }


}


export  {getUnit,getSensorType,getTimeWithOffset,getSensorBGColorClass};



